.app {
  width: 100%;
  min-height: 100vh;
  min-height: 100dvh;
  background: linear-gradient(-45deg, #3e1e68, #44bba4, #3a7ca5, #f2e863, #f45b69, #9261d1, #3e1e68);
  background-size: 1000% 1000%;
  animation: gradient 60s ease infinite;
  display: flex;
  overflow-y: auto;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
