.metric-highlight {
  text-transform: uppercase;
  font-size: 0.75em;
  background-color: rgba($color: white, $alpha: 0.6);
  padding: get-unit(1 2);
  margin: get-unit(0 1);
  border: get-color('gray', 1, 0.4) 1px solid;
  border-radius: $border-radius;
  white-space: nowrap;

  &--negative {
    border: get-color('red', 7, 0.8) 1px solid;
    background-color: get-color('red', 2, 0.6);
  }

  &--positive {
    border: get-color('yellow', 7, 0.8) 1px solid;
    background-color: get-color('yellow', 2, 0.6);
  }
}
